.Entry_header__2VG-v {
  grid-column: 1;
  grid-row: 1;

  width: 100%; height: 13%;
  background: #FFF;
  
  box-shadow: 0 0 5px #eee;

  display: grid;

  grid-template-columns: 85% 15%;
  grid-template-rows: 100%;
}
 .Entry_header__2VG-v>span {
   height: 42px;
   font-family: 'Concert One';
   font-weight: 500;
   font-size: 32px;

   letter-spacing: 2.5px;
   color: #353535;
   
   margin: auto 45px;
 }

 .Entry_terms__2IE63 {
   width: 100px; height: 60px;
   background: rgba(200, 200, 200, 0.2);
   border: 2px solid #eee;
   border-radius: 5px;
   
   transition: background-color .6s;

   align-self: center;
   justify-self: center;
 }
 .Entry_terms__2IE63:hover {
   background: none;
   border: 2px solid #eee;

   transition: background-color .6s;
 }
  .Entry_terms__2IE63>span {
    font-family: Dosis;
    font-weight: 600;
    font-size: 18px;
    
    letter-spacing: 2px;
    color: #353535;
  }
 
.Entry_content__3tKfM {
  grid-column: 1;
  grid-row: 1;

  width: 100%; height: 87%;
  align-self: end;
  
  overflow-x: hidden;
  overflow-y: auto;
}
 dl {
   width: 50%; height: 800px;
   padding: 5% 0 0 15%;
 }
  dt {
    margin: 15px 0;
  }

   dd {
     padding: 0 0 0 25px;
     margin: 10px 0;

     list-style-type: circle;
   }
   dd::before {
     content: '●';
     color: rgb(66, 208, 243);
   }
  

.Terms_close__17GNJ {
  position: fixed;
  top: 15px; right: 20px;
  width: 35px; height: 35px;

  background: rgba(245, 29, 51, 1);
  border: none;
  border-radius: 5px;
  
  transition: transform .1s;
  z-index: 99;
}

.Terms_close__17GNJ:active {
  transform: scale(0.9);
  transition: transform .1s; 
}
 .Terms_close__17GNJ>span {
   font-family: 'Concert One';
   font-weight: 600;
   font-size: 20px;

   color: #FFF;  
 }

.Terms_blank__4XtH4 {
  grid-column: 1;
  grid-row: 1;

  width: 50%; height: 100%;
  background: #FFF;

  align-self: center;
  justify-self: center;

  display: grid;

  grid-template-columns: 100%;
  grid-template-rows: minmax(500px, 1000px) 100px;
}
 .Terms_i-read__39eIO {
   width: 100px; height: 45px;
   background: rgba(200, 200, 200, 0.2);
   border: 2px solid #eee;
   border-radius: 5px;
   
   transition: background-color .6s;

   align-self: center;
   justify-self: center;
 }
 .Terms_i-read__39eIO:hover {
   background: none;
   border: 2px solid #eee;
   transition: background-color .6s;
 }
  .Terms_i-read__39eIO>span {
    font-family: Rajdhani;
    font-weight: 600;
    font-size: 16px;
    
    text-transform: uppercase;
    letter-spacing: 1.5px;
    color: #353535;
  }
body {
  width: 100vw; height: 100vh;
  position: fixed;
  background: rgba(235, 235, 235, 0.2);
}
 * { margin: 0; padding: 0; outline: none; }
 #root {
   width: 100%; height: 100%;
   
   overflow-x: hidden;
   overflow-y: auto;
   
   display: grid;

   grid-template-columns: 100%;
   grid-template-rows: 100%;
 }

