.header {
  grid-column: 1;
  grid-row: 1;

  width: 100%; height: 13%;
  background: #FFF;
  
  box-shadow: 0 0 5px #eee;

  display: grid;

  grid-template-columns: 85% 15%;
  grid-template-rows: 100%;
}
 .header>span {
   height: 42px;
   font-family: 'Concert One';
   font-weight: 500;
   font-size: 32px;

   letter-spacing: 2.5px;
   color: #353535;
   
   margin: auto 45px;
 }

 .terms {
   width: 100px; height: 60px;
   background: rgba(200, 200, 200, 0.2);
   border: 2px solid #eee;
   border-radius: 5px;
   
   transition: background-color .6s;

   align-self: center;
   justify-self: center;
 }
 .terms:hover {
   background: none;
   border: 2px solid #eee;

   transition: background-color .6s;
 }
  .terms>span {
    font-family: Dosis;
    font-weight: 600;
    font-size: 18px;
    
    letter-spacing: 2px;
    color: #353535;
  }
 
.content {
  grid-column: 1;
  grid-row: 1;

  width: 100%; height: 87%;
  align-self: end;
  
  overflow-x: hidden;
  overflow-y: auto;
}
 dl {
   width: 50%; height: 800px;
   padding: 5% 0 0 15%;
 }
  dt {
    margin: 15px 0;
  }

   dd {
     padding: 0 0 0 25px;
     margin: 10px 0;

     list-style-type: circle;
   }
   dd::before {
     content: '●';
     color: rgb(66, 208, 243);
   }
  
