body {
  width: 100vw; height: 100vh;
  position: fixed;
  background: rgba(235, 235, 235, 0.2);
}
 * { margin: 0; padding: 0; outline: none; }
 #root {
   width: 100%; height: 100%;
   
   overflow-x: hidden;
   overflow-y: auto;
   
   display: grid;

   grid-template-columns: 100%;
   grid-template-rows: 100%;
 }
